.single-events,
.tax-events_category {

	.qodef-ttevents-single-heading {
		@include qodefRelativeHolderLayout();
	}

	.qodef-ttevents-single-image-holder {
		img {
			display: block;
		}
		
		@include qodefImageZoomHoverStyle()
	}

	.qodef-event-list-category-holder {
		position: absolute;
		bottom: 33px;
		left: 0;
		padding: 6px 26px;
		background-color: $first-main-color;
		z-index: 0;

		a {
			position: relative;
			font-family: $heading-text-font;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: -0.025em;
			text-transform: uppercase;

			&:hover {
				color: inherit;
			}
		}

		&:before {
			content: '';
			position: absolute;
			display: block;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			z-index: -1;
			background-color: $first-main-color;
			transform: skew(-15deg, 0deg);
			transform-origin: bottom left;
		}
	}

	.qodef-single-event-info-date {
		display: block;
		margin-top: 29px;

		> a {
			font-family: $heading-text-font;
			font-size: 12px;
			font-weight: 700;
			text-transform: uppercase;
		}
	}

	.qodef-ttevents-single-title {
		margin-top: 3px;
		margin-bottom: 11px;
	}

	.qodef-event-single-icon {
		float: left;
		margin-right: 10px;
		color: $first-main-color;
		font-size: 16px;
	}

	.qodef-ttevents-single-subtitle {
		margin: 0 0 17px;
		color: $first-main-color;
	}

	.qodef-ttevents-single-content {
		margin-top: 10px;
	}

	.qodef-author-description {
		margin-top: 51px;

		.qodef-author-description-inner {
			padding: 69px 0 85px;
			min-height: 86px;
		}

		.qodef-author-description-content {
			padding: 0 30px;
		}

		.qodef-author-description-image {
			width: 86px;
		}

		.qodef-author-description-text-holder {
			width: 60%;
			padding: 0 0 0 30px;

			.qodef-author-quote {
				font-size: 20px;
			}

			.qodef-author-name {
				margin: 19px 0 0 0;
			}
		}
	}

	.qodef-related-events-holder {
		@include qodefRelativeHolderLayout();
		padding: 82px 0 100px;
		box-sizing: border-box;

		.qodef-related-events-title {
			margin: 0 0 21px;
		}

		.qodef-related-events-inner {
			margin: 0 -15px;
		}

		.qodef-related-event {
			position: relative;
			display: inline-block;
			float: left;
			width: 50%;
			margin: 0 0 30px;
			padding: 0 15px;
			box-sizing: border-box;

			.touch & {
				cursor: pointer;
			}

			.qodef-related-event-inner {
				min-height: 151px;
				background-color: #fbfafa;
			}

			.qodef-related-event-image {
				@include qodefRelativeHolderLayout();
				float: left;
				width: auto;

				a {
					position: relative;
					display: block;
				}

				img {
					display: block;
					width: 116px;
					height: 151px;
					object-fit: cover;
				}
			}

			.qodef-related-event-info {
				float: left;
				padding: 32px 0 0 25px;
			}

			.qodef-event-title {
				margin: 0;
				
				a:hover {
					color: $first-main-color;
				}
			}

			.qodef-single-event-info-date {
				margin-top: 1px;
			}

			.qodef-related-event-author {
				display: block;
				margin-top: 10px;

				> a {
					font-family: $heading-text-font;
					font-size: 14px;
					font-weight: 700;
					text-transform: uppercase;
				}
			}
		}
	}

	.tt_event_columns {

		.tt_event_column_left {
			width: 45%;
			margin-right: 0px;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;
		}

		.tt_event_column_right {
			width: 55%;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;

			h4{
				font-family: $heading-text-font;
				font-weight	: 600;
				font-size: 30px;
				color: $default-heading-color;
				margin-top: 33px;
			}

			p {
				color: $first-main-color;
				font-size: 16px;
				font-family: $default-text-font;
			}

		}
	}

	h3.tt_event_margin_top_27 {
		line-height: 35px;
		padding-top: 30px;
		padding-bottom: 5px;
        border-top: 1px solid #e5e5e5;
		margin-top: 36px !important;

        .tt_event_hours_count {
            color: inherit;
            font-weight: inherit;
        }
	}

	.tt_event_hours li {
		border: none;
		background-color: #f5f5f5;
		padding: 39px 30px 38px;;
		margin-bottom: 2.8%;
		width: 48%;
		margin-right: 2.8%;
		font-size: 10px;
		color: $default-text-color;
		letter-spacing: 0.2em;
		text-transform: uppercase;

        border-radius: 5px;
		box-sizing: border-box;

		&:nth-child(2n) {
			margin-right: 0;
		}

		h4 {
			margin: 0;
			text-transform: uppercase;
			font-size: 12px;
            color: $first-main-color;
			font-family: $default-text-font;
			font-weight: 400;

			&:nth-of-type(1) {
				margin-bottom: 11px;
				color: $first-main-color;
				font-size: 12px;
				font-family: $default-text-font;
				font-weight: 400;
				letter-spacing: 0.2em;
			}
		}

		p {
			margin-top: 0;
			font-family: $default-text-font;
			font-weight: 400;
			text-transform: none;
			color: $first-main-color;
			padding-top: 10px;
            font-size: 16px;
            letter-spacing: 0;
		}
	}

	.tt_event_items_list {
		margin: 0;

		li.type_info {
			border-bottom: 1px dashed #cdcdcd;
			padding: 0;
            margin-bottom: 22px;

			p {
				display:inline;
			}

			.tt_event_text {
				font-family: $default-text-font;
				color: $default-text-color;
				font-size: 16px;
				font-weight: 400;
				line-height: 20px;
				text-align: left;
				margin-left: 20px;
			}

			label {
				width: 163px;
				height: 21px;
				font-family: $heading-text-font;
				font-size: 16px !important;
				text-transform: uppercase;
				color: $default-heading-color;
			}
		}

		li:not(.type_info) {
			background: none;
			font-family: $default-text-font;
			font-size: 16px;
			color: $first-main-color;
			margin-bottom: 15px;
			padding-left: 27px;
			position: relative;

			&:before {
				content: '\4e';
				position: absolute;
				left: 0;
				font-family: 'ElegantIcons';
				color: $first-main-color;
				font-size: 18px;
			}
		}
	}
}

.tt_event_theme_page {

	h3 {
		font-family: $heading-text-font;
		font-weight	: 600;
		font-size: 30px;
		color: $default-heading-color;
	}

	h5 {
		font-family: $heading-text-font;
		font-weight	: 600;
		font-size: 30px;
		color: $default-heading-color;
	}

	.tt_event_page_left,
	.tt_event_page_right {
		padding-left: 25px;
		padding-right: 25px;
		box-sizing: border-box;
	}

	p{
		color: $first-main-color;
	}
}

.tt_event_page_left {
	width: 75%;
}

.tt_event_page_right {
	width: 25%;
}

.qodef-grid-1200 {

	.tt_event_theme_page {
		width: 1200px;
	}
}

.qodef-grid-1300 {

	.tt_event_theme_page {
		width: 1300px;
	}
}

// Events archive

.tax-events_category {
	article {
		margin-bottom: 90px;
		
		.qodef-ttevents-single-holder {
			padding: 0 0 0 26px
		}
	}
}
