$timetable-background-color: #302f34;
$timetable-border-color: #232226;

table.tt_timetable {
    font-weight: 400;
    color: #808080;
    font-size: 16px;
    border-collapse: collapse !important;

    thead tr.row_gray {
        background-color: #252429 !important;
        border: none;
    }

    tbody tr {
        border: none;
        border-top: 1px dashed $timetable-border-color;
        border-bottom: 1px dashed $timetable-border-color;
        background-color: $timetable-background-color !important;
        &:last-child {
            border-bottom: none !important;
        }
    }

    td.tt_hours_column {
        font-size: 12px;
        font-weight: 400;
        line-height: 1.6em;
        letter-spacing: 2px;
        font-family: $default-text-font;
        color: #fff;
    }

    th {
        color: #fff;
        height: 83px;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 400;
        line-height: 1.6em;
        letter-spacing: 2px;
        font-family: $default-text-font;
        box-sizing: border-box;
    }

    td {
        font-weight: 400;
        color: #808080;
        font-size: 12px;
        height: 120px;
        &:not(:last-child) {
            border-right: 1px solid $timetable-border-color !important;
        }
    }

    .row_gray {
        background-color: $timetable-background-color !important; //important because plugin css is also using important
    }

    .event_container {
        padding: 27px 10px 15px;
        height: 100%;
        box-sizing: border-box;
    }

    .event {
        background-color: transparent;
        color: #808080;
        vertical-align: middle;

        .event_header {
            font-size: 20px;
            line-height: 1.25em;
            letter-spacing: 1px;
            text-transform: uppercase;
            font-family: $heading-text-font;
            font-weight: 600;
            color: #fff;
            margin-bottom: 3px;
        }

        .top_hour,
        .bottom_hour {

            display: inline-block;
            margin: 0;
            float: left;
            width: 50%;
            box-sizing: border-box;

            .hours {
                color: #fff !important;
            }
        }

        .top_hour {

            text-align: right;
            padding-right: 5px;
            position: relative;

            &:after {
                position: absolute;
                content: "-";
                right: -2px;
                color: #fff;
            }
        }

        .bottom_hour {
            text-align: left;
            padding-left: 5px;
        }

        .before_hour_text,
        .after_hour_text {
            font-size: 14px;
            color: #717274;
        }

        .before_hour_text {
            display: none;
        }

        .after_hour_text {
            display: inline-block;
            margin-top: 6px;
            letter-spacing: 0.1em
        }

        &:not(.tt_single_event) {
            .event_container {
                &:last-child {
                    border-bottom: none;
                }
            }
        }


        hr {
            display: none;
        }

        &:hover, .event_container.tt_tooltip:hover {
            background-color: transparent;
        }

        &.tt_tooltip:hover .hours, .event_container.tt_tooltip:hover .hours {
            color: #808080;
        }

        a:hover, a.event_header:hover {
            text-decoration: none;
        }
    }

    .tt_tooltip_text {
        top: auto !important;
        left: 50% !important;
        bottom: calc(100% - 20px) !important;
        display: inline-block;
        height: auto !important;
        box-sizing: border-box;
        width: 133px;
        transform: translateX(-50%);

        .tt_tooltip_content {
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            padding: 9px 18px 5px 18px;
            margin: 0;
            font-family: $heading-text-font;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            box-sizing: border-box;
            border-radius: 0;
            letter-spacing: 1px;

            a {
                display: none;

            }
        }

        .tt_tooltip_arrow {
            border-color: $first-main-color transparent;
            border-width: 8px 8px 0;
            bottom: 0;
            z-index: 2;
            display: block;
            height: 0;
            width: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }

}

.tt_navigation_wrapper .tt_navigation_cell {
    margin: 0 auto !important;
}

.tt_tabs {
    text-align: center;

    .tt_tabs_navigation {
        display: inline-block;

        margin-bottom: 14px;

        li {

            margin: 0 20px 20px 20px !important;
            float: none;
            display: inline-block;
        }

        li a {
            background-color: transparent;
            font-size: 12px !important;
            font-weight: 400;
            line-height: 1.6em;
            letter-spacing: 2px;
            font-family: $default-text-font;
            color: $default-heading-color !important;
            text-transform: uppercase;
            box-sizing: border-box;
            padding: 7px 28px !important;
            border: none !important;
            margin: 0;

            -webkit-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            -moz-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
        }

        .ui-tabs-active a,
        li a:hover {
            background-color: $first-main-color;
            color: #fff !important;
        }
    }
}

#qodef-timetable-transparent {

    table.tt_timetable {
        .event {
            background: #302f34 !important;
        }
    }

    &.tt_tabs .tt_tabs_navigation li a {
        color: #fff !important;
    }
}