@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 52px 0 20px;
    }
    
    .qodef-comments-title {
        margin: 43px 0 82px;
    }

    .qodef-comment {

        &.qodef-pingback-comment {

            .qodef-comment-text {
                padding: 0;
            }

            .comment-edit-link {
                margin-top: 0;
                margin-right: 15px;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include qodefRelativeHolderLayout();
            margin: 7px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .qodef-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 79px;
        height: 79px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 3px 0 0 101px;
        min-height: 79px;

        > a:first-of-type {
            margin-left: 0;
        }

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            width: auto;
            padding-right: 31px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            margin-top: 5px;
            margin-left: 10px;
            font-family: $heading-text-font;
            font-size: 14px;
            font-weight: 700;
            color: $default-heading-color;
            text-transform: uppercase;

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-comment-name {
            margin: 0;
        }

        .qodef-comment-date {
            display: inline-block;
            margin: 0;
            font-size: 12px;
        }

        .qodef-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }

            .comment-form-cookies-consent {
                margin: 10px 0 28px 0;

                #wp-comment-cookies-consent {
                    vertical-align: text-bottom;
                }
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    @include qodefRelativeHolderLayout();
    margin: 38px 0 40px;
    clear: both;

    .comment-reply-title {
        margin: 29px 0 18px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    .comment-form-cookies-consent {
        margin: 10px 0 28px 0;

        #wp-comment-cookies-consent {
            vertical-align: text-bottom;
        }
    }
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {

            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include qodefButtonDefaultStyle();
    @include qodefButtonSize();
    @include qodefButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include qodefButtonSolidHoverColor();
    }
}

button.wpcf7-submit {

    &:after {
        content: "";
        @include qodefAbsoluteHolderLayout();
        background-color: $lighter-main-hover-color;
        transform: scaleX(0);
        transform-origin: 100% 0;
        transition: transform .4s cubic-bezier(0.72, 0.07, 0.22, 0.89);
        z-index: 1;
    }

    &:hover {
        background-color: $first-main-color !important;

        &:after {
            transform: scaleX(1);
            transform-origin: 0 0;
        }
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.qodef-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        .owl-item {
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                transition: background-color .2s ease-out, border .2s ease-out;
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: calc(100vh - 80px);
        background-color: rgba(0, 0, 0, 0.46);
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 112px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .qodef-404-title {
            margin: 0;
            font-family: $additional-text-font;
            font-size: 180px;
	        line-height: 1.05em;
            font-weight: 400;
            color: $default-background-color;
        }

        .qodef-404-subtitle {
            margin: 22px 0 0;
            color: $default-background-color;

            &:after {
                content: '';
                display: block;
                margin: 30px auto 23px;
                width: 72px;
                height: 3px;
                background-color: $first-main-color;
            }
        }

        .qodef-404-text {
            margin: 10px 0 0;
            padding: 0 25%;
            font-family: $default-text-font;
            font-size: 20px;
            color: $default-background-color;
        }

        .qodef-btn {
            margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include laptop-landscape-mac {

        .qodef-page-not-found {
            padding: 0 20%;

            .qodef-404-text {
                padding: 0 10%;
            }
        }
    }

    @include ipad-landscape {
        .qodef-page-not-found {
            padding: 0 20%;
        }

        .qodef-content {
            height: calc(100vh - 85px);
        }

        &.admin-bar {
            .qodef-content {
                height: calc(100vh - 117px);
            }
        }
    }

    @include ipad-portrait {

        .qodef-page-not-found {

            .qodef-404-text {
                padding: 0;
            }
        }
	
	    &.admin-bar {
            .qodef-content {
                height: calc(100vh - 131px);
            }
        }
    }

    @include phone-landscape {

        .qodef-page-not-found {
	        padding: 0 15%;

            .qodef-404-title {
                font-size: 120px;
            }

            .qodef-404-subtitle {
	            margin-top: 6px;
                font-size: 25px;
            }
	
	        .qodef-404-text {
		        font-size: 17px;
	        }
        }
    }
	
	@include smaller-phone-portrait {
		
		.qodef-page-not-found {
		
			.qodef-404-title {
				font-size: 90px;
			}
			
			.qodef-404-subtitle {
				font-size: 20px;
			}
			
			.qodef-404-text {
				font-size: 15px;
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $elements-color-four;
        border: 1px solid $elements-color-four;
        overflow: hidden;

        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;

        i {
            transition: .2s ease;
        }

        &:hover {
            background-color: $elements-color-four;
            border: 1px solid $elements-color-four;

            i {
                transform: translateY(-4px) rotateY(180deg);

                transition: .5s cubic-bezier(0.72, 0.03, 0.25, 1);
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
    .qodef-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Custom Contact Form 7 - start
   ========================================================================== */

.qodef-cf7-newsletter-simple {

    input[type=email] {
        margin: 0 0 26px;
        padding: 0;
        font-size: 12px;
        border-top: none;
        border-left: none;
        border-right: none;
        border-bottom: 1px solid #666;

        @include qodefPlaceholder {
            color: #c0c0c0;
        }
    }
}

.qodef-cf7-newsletter-dark,
.qodef-cf7-newsletter-light {

    .qodef-cf7-nsl-col {
        margin-right: 40px;
        float: left;
        display: inline-block;
        width: 330px;
    }

    input[type=text],
    input[type=tel],
    input[type=email] {
        margin: 15px 0 0 0;
        padding: 0 0 5px 26px;
        font-size: 16px;
        border-top: none;
        border-left: none;
        border-right: none;
        border-bottom: 2px solid #555;

        @include qodefPlaceholder {
            color: #000;
        }

        &:focus {
            border-bottom: 2px solid #555;
        }
    }

    .wpcf7-form-control-wrap {

        &:before {
            display: inline-block;
            position: absolute;
            left: 0;
            bottom: -2px;
            font-family: ElegantIcons;
            font-size: 18px;
        }
    }

    .your-name {

        &:before {
            content: '\6a';
        }
    }

    .your-tel {

        &:before {
            content: "\e00b";
        }
    }

    .your-email {

        &:before {
            content: "\e010";
        }
    }

    .qodef-btn.qodef-btn-solid {
        margin-left: 32px;
        display: inline-block;
        float: left;
        background-color: #0a0a0a;
        color: $default-background-color;
    }

    @media (max-width: 1600px) {
    
        display: flex;
        justify-content: space-between;
    
        p {
            display: flex;
            align-items: flex-start;
            flex-direction: row-reverse;
        }
        
        .qodef-cf7-nsl-col {
            width: 24.5%;
    
            body.qodef-has-anchor-menu & {
                width: 24%;
            }
        }
	
	    .qodef-btn.qodef-btn-solid {
		    margin-left: 0;
	    }
    }

    @include ipad-landscape {

        .qodef-cf7-nsl-col {
	        width: 23%;
            margin-right: 20px;
	
	        body.qodef-has-anchor-menu & {
		        width: 23%;
	        }
        }

        .qodef-btn.qodef-btn-solid {
            margin-left: 12px;
        }
    }

    @include ipad-portrait {
	    
	    flex-wrap: wrap;
	    

        .qodef-cf7-nsl-col {
            margin: 0;
            width: 100%;
	
	        body.qodef-has-anchor-menu & {
		        width: 100%;
	        }
        }
	
	    p {
		    flex-direction: column;
		    width: 100%;
	    }

        .qodef-btn.qodef-btn-solid {
            margin-top: 30px;
            margin-left: 0;
	        width: 100%;
        }
    }
}

.qodef-cf7-newsletter-dark {
	
	
	input[type=text],
	input[type=tel],
	input[type=email] {
		border-color: #0a0a0a;
	}

    .qodef-btn.qodef-btn-solid:not(.qodef-btn-custom-hover-color) {
	
	    &:after {
		    background-color: #171717;
	    }

        &:hover {
            background-color: #0a0a0a !important;
            border-color: #0a0a0a !important;
            color: #fff !important;
        }
    }
}

.qodef-cf7-newsletter-light {

    input[type=text],
    input[type=tel],
    input[type=email] {
        border-bottom: 2px solid $default-background-color;
        color: $default-background-color;

        @include qodefPlaceholder {
            color: $default-background-color;
        }

        &:focus {
            color: $default-background-color;
            border-bottom: 2px solid $default-background-color;
        }
    }

    .wpcf7-form-control-wrap {

        &:before {
            color: $default-background-color;
        }
    }

    .qodef-btn.qodef-btn-solid {
        background-color: #fed620;
        color: #0a0a0a;
    }
}

.qodef-cf7-application {

    input[type=text],
    input[type=email],
    input[type=tel] {
        margin: 0 0 17px;
        padding: 13px 21px;
        cursor: auto;

        &:focus {
            border: 1px solid #000;
        }
    }

    .qodef-btn.qodef-btn-solid {
        margin: 3px auto 0 auto;
        display: block;
    }
}

.qodef-cf7-contact {

    input[type=text],
    input[type=email],
    textarea.wpcf7-textarea {
        margin: 0 0 14px;
        padding: 10px 16px;
    }

    textarea.wpcf7-textarea {
        height: 161px;
    }

    .qodef-btn.qodef-btn-solid {
        top: -22px;
    }
}

.qodef-cf7-side-area {

    input[type=text],
    input[type=email] {
        margin: 0 0 14px;
        padding: 10px 16px;
        width: 90.5%;
	
	    @include qodefPlaceholder {
		    color: #000;
	    }
    }

    textarea.wpcf7-textarea {
        height: 161px;
    }
}

/* ==========================================================================
   #Custom Contact Form 7 - end
   ========================================================================== */

/* ==========================================================================
   #Row Background styles - begin
   ========================================================================== */

.qodef-row-has-bg-text {
    position: relative;
}

.qodef-row-bg-text-holder {
    @include qodefAbsoluteHolderLayout();

    .qodef-row-bg-text-wrapper {
	    position: relative;
	    width: 100%;
	    display: flex;
	    flex-flow: column;
	
	    &.qodef-row-bg-text-align-left {
		    align-items: left;
	    }
	    &.qodef-row-bg-text-align-center {
		    align-items: center;
	    }
	    &.qodef-row-bg-text-align-right {
		    align-items: flex-end;
	    }

        .qodef-row-bg-text-wrapper-inner {
            position: relative;
            vertical-align: middle;
            font-family: $heading-text-font;
            font-size: 569px;
            color: transparent;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: #f3f3f3;
            overflow: hidden; //Fixes overlapping content above this row

            .qodef-row-bg-text {
                font-weight: 700;
                line-height: 1;
                user-select: none;
	            display: inline-block;
            }
        }

        @include ipad-landscape {
            display: none;
        }

        &.qodef-row-bg-text-animation {
            visibility: hidden;

            .no-touch & {

                .qodef-char-mask {
	                display: table-cell;
                    vertical-align: middle;
                    overflow: hidden;

                    > span {
                        display: inline-block;
                        transform: translate3d(-100%, 0, 0);
                        transition: all .5s cubic-bezier(0.05, 0.43, 0.04, 0.87);
                    }
                }

                &.qodef-row-background-text-align-right {

                    .qodef-char-mask > span {
                        transform: translate3d(100%, 0, 0);
                    }
                }

                .qodef-char-mask > span.qodef-show {
                    transform: translate3d(0, 0, 0);
                }
            }
        }
    }
}

/* ==========================================================================
   #Row Background styles - end
   ========================================================================== */

.qodef-pattern-overlay-custom  {
    
    .qodef-eh-item-background {
        left: -26px;
        top: 34px;
        transform: translate3d(26px, -34px, 0); 
    }

    &.qodef-pattern-appear {

        .qodef-eh-item-background {
            transition: .5s .5s;
            transform: translate3d(0, 0, 0);
        }
    }
}